$color-key-primary : #F5F6FA;
$color-key-shadow : #D5D6DA;
$color-key-shadow-lite : #fafbff;

$color-key-border : #555658;
$color-key-background: #C5C6C8;
$color-key-sign: #868686;


.keyboard-container{
    padding: 6px;
    width: 680px;
    height: 270px;
    background-color: $color-key-background;
    border-radius: 20px;
    // gap:4px 4px;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    justify-content: space-around;

}
.key{
    background: linear-gradient(180deg, $color-key-primary 0%, $color-key-shadow 15%,$color-key-shadow-lite 100%);
    min-width: 40px;
    height: 40px;
    border-radius: 6px;
    margin:1.5px;
    border : 1.5px ridge rgba(85,86,88,.9);
    box-shadow: 1px 1px 1px $color-key-sign;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $color-key-sign;
}
